<template>
    <div>
        <Row>
            <Button type="primary" @click="add" v-permission.show="'b:perm:platform:create'">
                <Icon type="plus"></Icon>新增短信模板
            </Button>
        </Row>
        <Row class="margin-top-10">
            <label class="margin-left-10" style="margin-right:8px;">模板编号：</label>
            <Input v-model="table.query.template" placeholder="" clearable style="width: 200px" />
            <label class="margin-left-10" style="margin-right:8px;">标题：</label>
            <Input v-model="table.query.title" placeholder="" clearable style="width: 200px" />
            <label class="margin-left-10" style="margin-right:8px;">类型：</label>
            <Select v-model="table.query.businessType" clearable style="width: 200px">
                <Option v-for="item in SmsBusinessType" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdate" @on-cancel="cancel">
            <Form ref="form" :model="vo" :label-width="90" :rules="validate">
                <Form-item label="标题" prop="title">
                    <Input v-model.trim="vo.title" />
                </Form-item>
                <Form-item label="短信内容" prop="content">
                    <Input v-model="vo.content" type="textarea" />
                </Form-item>
                <Form-item label="阿里云编号" prop="template">
                    <Input v-model.trim="vo.template" />
                </Form-item>
                <Form-item label="类型" prop="type">
                    <Select transfer v-model="vo.type">
                        <Option v-for="item in SmsType" :value="item.id" :key="item.id">{{ item.name }}</Option>
                    </Select>
                </Form-item>
                <Form-item label="业务类型" prop="businessType">
                    <Select transfer filterable v-model="vo.businessType">
                        <Option v-for="item in businessTypes" :value="item.id" :key="item.id">{{ item.name }}</Option>
                    </Select>
                </Form-item>
                <FormItem label="状态" prop="status" v-if="isUpdate">
                    <i-switch size="large" v-model="vo.status">
                        <span slot="open">启用</span>
                        <span slot="close">禁用</span>
                    </i-switch>
                </FormItem>
                <Form-item label="描述" prop="memo">
                    <Input v-model="vo.memo" type="textarea" />
                </Form-item>
            </Form>
        </Modal>
    </div>
</template>

<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { SmsType, SmsBusinessType, Status } from '@/libs/dic';
export default {
    name: 'SmsTemplate',
    mixins: [ TableDataView, TableColRender, ModelView ],
    data () {
        return {
            SmsType,
            SmsBusinessType,
            Status,
            table: {
                col: [{
                    title: '标题',
                    key: 'title'
                }, {
                    title: '阿里云编号',
                    key: 'template'
                }, {
                    title: '短信内容',
                    key: 'content',
                    tooltip: true
                }, {
                    title: '类型',
                    key: 'businessType',
                    render: (h, params) => this.renderDic(h, params)(SmsBusinessType)
                }, {
                    title: '状态',
                    key: 'status',
                    render: this.renderStatus
                }, {
                    title: '创建时间',
                    key: 'createTime',
                    render: this.renderDate
                }, {
                    title: '更新时间',
                    key: 'updateTime',
                    render: this.renderDate
                }, {
                    title: '操作',
                    key: 'action',
                    width: 240,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' }, disabled: params.row.type === 0 },
                        click: () => this.update(params.row),
                        text: '修改'
                    }, {
                        type: 'switch',
                        click: () => this.status(params.row),
                        bool: params.row.status,
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' } }
                    }, {
                        config: { loading: this.loadingBtn, icon: 'md-trash' },
                        type: 'delete',
                        click: () => this.remove(params.row)
                    }])
                }],
                url: '/api/sms-template/list',
                query: {
                    template: null,
                    title: null,
                    type: null
                }
            },
            vo: {
                title: null,
                template: null,
                type: null,
                content: null,
                businessType: null,
                status: null,
                memo: null
            },
            validate: {
                title: [{ required: true, trigger: 'blur', message: '请填写标题' }],
                template: [{ required: true, trigger: 'blur', message: '请填写阿里云编号' }],
                type: [{ required: true, type: 'number', trigger: 'change', message: '请选择短信类型' }],
                content: [{ required: true, trigger: 'blur', message: '请输入短信内容' }],
                businessType: [{ required: true, type: 'number', trigger: 'change', message: '请选择业务类型' }]
            },
            addUrl: '/api/sms-template/add',
            updateUrl: '/api/sms-template/update',
            removeUrl: '/api/sms-template/remove'
        };
    },
    computed: {
        businessTypes () {
            return SmsBusinessType.filter(t => this.vo.type === 2 ? t.voucher : !t.voucher);
        }
    },
    methods: {
        status (item) {
            Object.keys(this.vo).filter(key => Reflect.has(item, key)).forEach(key => this.vo[key] = item[key]);
            this.vo.status = !item.status;
            this.doAddOrUpdate();
        }
    }
};
</script>

<style scoped>

</style>
